body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  height: 100%
}

.card {
  background-color: darkgray;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  padding: 25px;
  border-radius: 25px;
  border-color: darkcyan;
  border-width: 5px;
  border-style: groove;
  opacity: 0.89;
  height: 400px ;
  width: 600px ;
}

.card-img-top{ 
  height: 300px;
  width: 550px;
  border-radius: 25px;
  z-index: -1;
}

.Projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly; 
  margin-top: 100px;
}

.card-title{
  font-size: 2rem;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.card-text{
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

@media (max-width: 350px) {
  .card{
    height: 250px ;
    width: 200px ;
  }

  .card-img-top{ 
    height: 141px;
    width: 235px;
  }
}

@media (min-width: 350px) and (max-width: 760px) {
  .card{
    height: 350px ;
    width: 300px ;
  }

  .card-img-top{ 
    height: 241px;
    width: 300px;
  }
}

.App {
  text-align: center;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
} 

.ContactForm {
  text-align: center;
  max-width: 300px;
  margin: 20px auto;
}

Form {
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 20px;
}

.videoTag { 
  position: fixed;
  min-height: 100%;
  min-width: 100%;
  z-index: -1;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  object-fit: cover;
  object-position: center;
}

.content-container{
  min-height: 100vh;
}

.About-me {
  position: relative;
  margin: 5rem;
  background-color: darkgray;
  padding: 10px;
  border-radius: 10px;
  border-color: black;
  border-width: 5px;
  border-style: groove;
  opacity: 0.95;
}

.About-me h2{
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 4rem;
  text-align: center;
  -webkit-text-decoration: double;
          text-decoration: double;
}

.about-content{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.myPhoto{
  height: 350px;
  width: 350px;
  border-radius: 50%;
  margin: 20px;
}

.about-text{ 
  padding: 15px;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 22px;

}

.stock-img{
  height: 150px;
  width: 150px;
  border-radius: 50%;
}


.Projects{
  overflow-y: scroll;
  position: relative;
  margin-bottom: 15px 10px 100px 10px;
}

.footer--pin {
  display: grid;
  width: 100%;
  height: 95px;
  bottom: 0;
  right: 0;
  left: 0; 
  background-color:darkgrey ;
  opacity: 0.9;
  align-content: center;

}

div.icon-container {
  display: flex ;
  justify-content: space-evenly;
}

svg { 
  height: 4rem;
  width: 4rem; 
  color:dodgerblue; 
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px){
  .about-text {
    font-size: 17px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
  }

  .myPhoto{
    height: 200px;
    width: 200px;
    opacity: 1;
    text-align: center;
  }

  .About-me{
    margin: 25px;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 279px) {
  .about-text{ 
    padding: 15px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 20px;
    width: 500px;
    text-align: center;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .about-text{ 
    padding: 15px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 22px;
    width: 700px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .about-text{ 
    padding: 15px;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    font-size: 22px;
    min-width: 500px;
  }
}

@media only screen and (min-width: 1310px) {
  .about-text{ 
    text-align: justify;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1310px) {
  .about-text{ 
    padding: 15px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 22px;
    
  }
}

@media only screen and (min-width: 1709px) {
  .about-text{ 
    padding: 15px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 22px;
    width: 1100px;
  }
}



@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

div.nav.nav-tabs{
  display: flex;
  justify-content: right;
}
